import noconetnt from '@/components/noconetnt.vue'
export default {
	data() {
		return {
			list:[]
		};
	},
	onShow(){
		this.getinfo();
	},
	methods:{
		getinfo(){
			uni.request({
				url: this.$api+'/weixin/shishenxin/mychragelist',
				data: this.getmd5({
					openId:uni.getStorageSync('openId'),
					// openId:'onR7C05SKrVWwNSZrlQgPbMEL4t4'
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					if (res.data.code != 200) {
						uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
						});
					} else {
						uni.hideLoading();
						this.list = [];
						var data = res.data.data;
						this.list = data.data;
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
	},
	components:{
		noconetnt
	}
}
